import React from "react"
import styled from "styled-components"
import { device } from "../config/config"

const StyledContainer = styled.div`
  @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");
  font-family: "Poppins", sans-serif;

  h3 {
    color: hsl(184, 74%, 44%);
    font-size: 2rem;
    text-align: center;
  }

  @media only screen and ${device.tablet} {
    h3 {
      font-size: 1.5rem;
      margin: 1rem 0;
    }
    p {
      margin-bottom: 0;
    }
  }
`

const StyledDiv = styled.div`
  display: flex;
  flex-direction: row;
  height: "100px"
  width: 80%;
  background: #07093e;
  margin: 0 auto;
  overflow-x: hidden;

  .mernContainer {
    position: relative;
    width: 100%;
    overflow-x: hidden;
  }

  .mernContainer.mernContainer::after {
  content: '';
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 150px;
  height: calc(100% - 20px);
  right: 0;
  position: absolute;
  z-index: 9999999;
  margin-left: auto;
  top: 0;
  background: linear-gradient(to right, #07093e00 0, #07093eB3 40%, #07093e 70%);
}
  .mernContainer.mernContainer::before {
  content: '';
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 150px;
  height: calc(100% - 20px);
  left: 0;
  position: absolute;
  z-index: 9999999;
  margin-left: auto;
  top: 0;
  background: linear-gradient(to left, #07093e00 0, #07093eB3 40%, #07093e 70%);
}

.mern {
  display: block;
  width: 100%;
  transform: translateX(100%); 
  animation: move 20s linear infinite
}

/* Create the animation */
@keyframes move {
  to {
    transform: translateX(-205%);
  }
}
@keyframes moveTablet {
  to {
    transform: translateX(-100%);
  }
}
@keyframes moveMobile {
  to {
    transform: translateX(-75%);
  }
}

svg { 
  width: 60%;
}

  svg,
  div {
    margin: 0 auto;
  }

  p {
    font-size: 3rem;
    text-overflow: clip;
    overflow: hidden;
    white-space: nowrap;
  }

  svg,
  path {
    fill: hsl(184, 74%, 44%);
  }

  @media only screen and ${device.tablet} {
    width: 100%;
    margin: 0;

    .mern {
    transform: translateX(75%); 
    animation: moveTablet 20s linear infinite
  }

    ${
      "" /* .mernContainer.mernContainer::after,
    .mernContainer.mernContainer::before {
      width: 75px;
    } */
    }

    p {
      font-size: 2rem;
    }
  }

  @media only screen and (max-width: 600px) {
    width: 100%;

    .mern {
      transform: translateX(100%); 
      animation: moveMobile 20s linear infinite;
    }

    svg {
      width: 50%;
    }
  }
`

